.Home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
  }
  .Home__row{
    display:flex;
    z-index:1;
    margin-left:5px;
    margin-right: 5px;

  }
  .Home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)); 
    /* go from complete color to opaque */
  }
  